.route-progress-middle {
    position: relative;
    width: 15px;
    border: 1px solid #BDBDBD;
    margin-top:0;
    margin-bottom:0;
    margin-left:auto;
    margin-right:auto;
    background-color: white;
}

.route-progress-start {
    position: relative;
    width: 15px;
    border-radius: 0px 0px 30px 30px;
    border: 1px solid #BDBDBD;
    margin-top:0;
    margin-bottom:10px;
    margin-left:auto;
    margin-right:auto;
    background-color: white;
}

.route-progress-end {
    position: relative;
    width: 15px;
    border-radius: 30px 30px 0px 0px;
    border: 1px solid #BDBDBD;
    margin-top:10px;
    margin-bottom:0;
    margin-left:auto;
    margin-right:auto;
    background-color: white;
}

.filler {
    background: #1DA598;
    height: 100%;
    border-radius: inherit;
    transition: height .5s ease-out;
    position: absolute;
    bottom: 0;
    width: -moz-available;
}

#descent-segment {
    color: #1DA598;
    transition: color .5s ease-out;
}

#approach-segment {
    margin: 0px 0px 15px 5px;
    color: #1DA598;
}